import type { MetaFunction } from "@remix-run/node";
import {
  Links,
  LiveReload,
  Meta,
  Outlet,
  Scripts,
  ScrollRestoration,
} from "@remix-run/react";
import { ToastContainer } from "react-toastify";
import toastCss from "react-toastify/dist/ReactToastify.css";
import { RecoilRoot, useRecoilCallback } from "recoil";
import { Heart } from "./src/icons";
import Navbar from "./src/layout/MainLayout/Navbar";
import Button from "./src/ui/Button";
import styles from "./styles/global.css";

export function links() {
  return [
    { rel: "stylesheet", href: styles },
    { rel: "stylesheet", href: toastCss },
    {
      rel: "icon",
      href: "/favicon.png",
      type: "image/png",
    },
  ];
}

export const meta: MetaFunction = () => ({
  charset: "utf-8",
  title: "New Remix App",
  viewport: "width=device-width,initial-scale=1",
});
function DebugButton(): React.ReactElement {
  const onClick = useRecoilCallback(
    ({ snapshot }) =>
      async () => {
        console.debug("Atom values:");
        for (const node of snapshot.getNodes_UNSTABLE()) {
          const value = await snapshot.getPromise(node);
          console.debug((node.key, value));
        }
      },
    []
  );

  return (
    <Button
      className="fixed bottom-8 left-10 w-36 py-2"
      onClick={onClick}
      iconStart={<Heart />}
    >
      Log Recoil
    </Button>
  );
}
export default function App() {
  return (
    <html lang="en">
      <head>
        <Meta />
        <Links />
      </head>
      <body>
        <RecoilRoot>
          <Navbar></Navbar>
          <Outlet />
          <ToastContainer
            position="top-right"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            theme="light"
          />
          <ScrollRestoration />
          <Scripts />
          {process.env.NODE_ENV === "development" ? <DebugButton /> : null}
          {process.env.NODE_ENV === "development" ? <LiveReload /> : null}
        </RecoilRoot>
      </body>
    </html>
  );
}
