import { atom, selector } from "recoil";

const userInfo = atom({
  key: "useInfo",
  default: {},
});

const checkLogin = selector({
  key: "checkLogin",
  get: ({ get }) => {
    const user = get(userInfo);
    return Object.keys(user).length !== 0 && user.constructor === Object;
  },
});

export { checkLogin, userInfo };
