import { checkLogin, userInfo } from "@recoil_state/authState";
import { Link, useFetcher, useLoaderData } from "@remix-run/react";
import { useEffect } from "react";
import { useRecoilValue, useSetRecoilState } from "recoil";

export default function AuthSection() {
  const data = useLoaderData();
  const fetcher = useFetcher();
  const setUser = useSetRecoilState(userInfo);
  const isLogin = useRecoilValue(checkLogin);
  useEffect(() => {
    if (data) setUser(data);
    else setUser({});
  }, [data, setUser]);

  return (
    <div className="flex-none">
      {!isLogin ? (
        <ul className="menu menu-horizontal px-1">
          <li>
            <Link to="/ui-test">UI</Link>
          </li>
          <li>
            <Link to="/login">Login</Link>
          </li>
        </ul>
      ) : (
        <div className="dropdown dropdown-end">
          <label tabIndex={0} className="btn btn-ghost btn-circle avatar">
            <div className="w-10 rounded-full">
              <img src="https://placeimg.com/80/80/people" alt="avatar" />
            </div>
          </label>
          <ul
            tabIndex={0}
            className="mt-3 p-2 shadow menu menu-compact dropdown-content bg-base-100 rounded-box w-52"
          >
            <li>
              <Link to="/ui-test">UI</Link>
            </li>
            <li>
              <Link to="/child" className="justify-between">
                Profile
                <span className="badge">New</span>
              </Link>
            </li>
            <li>
              <fetcher.Form method="post" action="/logout">
                <button type="submit">Logout</button>
              </fetcher.Form>
            </li>
          </ul>
        </div>
      )}
    </div>
  );
}
